import api from '../../common/request.js'	

export default {
	data() {
		return {
			typelist: []
		}
	},
	onLoad() {
		this.getTypeData()
	},
	onReady() {
		
	},
	methods: {
		getTypeData() {
		  api.get('/book_category/getCategory', {
		    channel: 'man'
		  }).then(res => {
		    if (res.code === 200) {
					this.typelist = res.data
		    }
		  })
		}
	}
	
}
